#root {
    height: 85%;
}

textarea:focus,
input:focus {
    outline: none;
}

textarea {
    box-sizing: border-box;
    resize: none;
    font-family: "Lexend", sans-serif;
    font-size: 20px;
    padding: 10px 15px;
    border-radius: 4px;
    border: #463F3A 1px solid;
}

.contact {
    padding: 20px 40px 20px 40px;
}

.contact-title {
    font-size: 60px;
    text-shadow: 3px 3px 2px #E0AFA1;
    color: #463F3A;
}

.contact-regular-text {
    margin-top: 30px;
    font-size: 20px;
    color: #463F3A;
    margin-bottom: 30px;
}

.contact-form {
    display: flex;
    flex-direction: column;
    width: 70%;
}

.contact-name {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 20px;
}

.contact-name-box {
    width: 48%;
    line-height: 1;
}

.contact-email {
    margin-bottom: 20px;
}

.contact-message {
    height: 250px;
}

.contact-submit-button {
    width: 20%;
    border-radius: 4px;
    border: none;
    color: #463F3A;
    background-color: #E0AFA1;
    font-family: "Lexend", sans-serif;
    font-size: 20px;
    margin-top: 20px;
    align-self: end;
    padding: 5px;
    transition: ease-in-out 0.3s;
}

.contact-submit-button:hover {
    transform: scale(1.05);
    color: #F4F3ED;
}